import React, {Component} from "react";
import {PropTypes} from "prop-types";
import OraModal from "eshop/modules/core/components/ui/Modal";
import {Expander} from "eshop/modules/core/components/ui/Expander";
import {compareByProductListAndMandatoryAndTitle, Filters} from "../../../../analyzer/CartInfoUtils";
import {IfCollectionNotEmpty} from "./Utils";
import {SaveVasesButton, TitledFoldableProductGroup, TitledFoldableTvVasesProductGroup} from "./modalFragments/ModalFragments"
import {prepareDevices} from "../../../../analyzer/DeviceUtils";
import {Warning} from "../../shared/MessageBox";
import {CloseAndSaveVasesButtons} from "eshop/modules/cart/components/entriesList/convergent/vasModal/CloseAndSaveVasesButtons";

export default class FixVASModal extends Component  {
    constructor (props) {
        super(props);
        this.state = {
            open: this.props.open,
            popup: false,
            callback: null,
            event: null,
            modalContent: null
        }
    }

    componentWillReceiveProps(nextProps){

        this.setState({
            open: nextProps.open
        })
    }

    _onSaveWrapper(){
        this.props.onClose();
        this.props.onSave();
    }

    _onClickInterceptor(productCode, propositionId, modalContent) {
        return (event) => {
            this.setState({
                popup: true,
                callback: this.props.onClick(productCode, propositionId),
                event: event,
                modalContent: modalContent
            });
        }
    }

    _runInterceptedClick() {
        this.state.callback(this.state.event);
        this._cancelInterceptedClick();
    }

    _cancelInterceptedClick() {
        this.setState({
            popup: false,
            callback: null,
            event: null,
            modalContent: null
        });
    }

    render(){
        let vasesWithStatuses = this.props.services.map(vas => {
            vas.selected = false;
            vas.mandatory = false;
            vas.presentation = "visible";
            return vas;
        });
        Object.keys(this.props.entries)
            .filter(productCode => this.props.entries[productCode])
            .map(productCode => vasesWithStatuses.find(vas => vas.id === productCode))
            .filter(vas => vas)
            .forEach(vas => vas.selected = true);
        this.props.mandatories
            .filter(productCode => this.props.entries[productCode])
            .map(productCode => vasesWithStatuses.find(vas => vas.id === productCode))
            .filter(vas => vas)
            .forEach(vas => vas.mandatory = true);
        if(this.props.hidden) {
            this.props.hidden
                    .map(productCode => vasesWithStatuses.find(vas => vas.id === productCode))
            .filter(vas => vas)
            .forEach(vas => vas.presentation = "hidden");
        }

        return(
        <OraModal open={this.props.open} onClose={this.props.onClose} size="medium">
            <FixVASModalContent
                configurables = {this.props.configurables}
                descriptions = {this.props.descriptions}
                onClickWrapped={this._onClickInterceptor.bind(this)}
                onClick={this.props.onClick}
                productList={this.props.productList}
                selectedSection = {this.props.selectedSection}
                showNetPrices={this.props.showNetPrices}
                uid={this.props.uid}
                vasesWithStatuses={vasesWithStatuses}
                migratedProducts={this.props.migratedProducts}
            />
            {
                this.state.popup &&
                <Warning
                    onAccept={this._runInterceptedClick.bind(this)}
                    onCancel={this._cancelInterceptedClick.bind(this)}
                    modalContents={this.state.modalContent}
                />
            }
            <div className="l-row">
                <CloseAndSaveVasesButtons onClose={this.props.onClose} saveVasesClicked={this._onSaveWrapper.bind(this)}/>
            </div>
        </OraModal>)
    }
}

FixVASModal.propTypes = {
    uid: PropTypes.string,
    header: PropTypes.string,
    bundle: PropTypes.string,
    cartBundle: PropTypes.string,
    propositionId: PropTypes.string,
    open: PropTypes.bool,
    descriptions: PropTypes.object,
    services: PropTypes.array,
    entries: PropTypes.object,
    mandatories: PropTypes.array,
    productList: PropTypes.object,
    hidden: PropTypes.array
}

const FixVASModalContent = (props) => {

    function getSortedPresentableProductsOf(type, productsOrder){
        let products = Filters()[type](props.vasesWithStatuses);
        const presentableProducts = [
            ...products.filter(p => !!p.addonType && !p.mandatory),
            ...prepareDevices(products.filter(p => !!p.deviceType), props.descriptions, props.migratedProducts)];
        presentableProducts.sort(compareByProductListAndMandatoryAndTitle(productsOrder));
        return presentableProducts;
    }

    function renderSection(command){
        if (command.type  === "TV_VASES"){
            return renderTvVasesSection(command);
        }
        return renderCommonSection(command);
    }

    function renderTvVasesSection(command){
        return (
            <div>
                <IfCollectionNotEmpty array={command.products} key={"ICNE_" + command.index}>
                    <TitledFoldableTvVasesProductGroup
                        key={"TPG_" + command.index}
                        uid={"TPG_" + command.index}
                        idx={command.type}
                        section={command.section}
                        products={command.products}
                        onClick={command.onClick}
                        onClickWrapped={command.onClickWrapped}
                        isExpanded={command.isExpanded}
                        net={command.showNetPrices}
                        configurables={command.configurables}
                        descriptions={command.descriptions}
                    />
                </IfCollectionNotEmpty>
            </div>
        );
    }

    function renderCommonSection( command) {
        return (
            <IfCollectionNotEmpty array={command.products} key={"ICNE_" + command.index}>
                <TitledFoldableProductGroup
                    key={"TPG_" + command.index}
                    uid={"TPG_" + command.index}
                    idx={command.type}
                    section={command.section}
                    products={command.products}
                    onClick={command.onClick}
                    onClickWrapped={command.onClickWrapped}
                    isExpanded={command.isExpanded}
                    net={command.showNetPrices}
                    configurables={command.configurables}
                />
            </IfCollectionNotEmpty>
        );
    }

    return (
        <div>
            <p className="h3">Dobierz us≈Çugi dodatkowe</p>
            <Expander id={`exp-${props.uid}`} variant="section" className="opl-vas-modal-expander" scrollToSelected={false}>
                <div className="o-separator o-separator--m"/>
                {
                    props.productList &&
                    props.productList.innerLists &&
                    props.productList.innerLists.map((section, index) => {
                        let type = section.productFilter;
                        const isExpanded = props.selectedSection === section.productFilter;
                        let productsOrder = section.productCodes || [];
                        const presentableProducts = getSortedPresentableProductsOf(type, productsOrder);
                        const renderCommand = {
                            ...props,
                            index:  index,
                            section: section,
                            products: presentableProducts,
                            isExpanded: isExpanded,
                            type: type
                        };
                        return renderSection(renderCommand);
                    })
                }
            </Expander>
        </div>
    );
}